//
// Color
//

// basic
$color-primary-dark                   : #222222;
$color-primary-light                  : #FCFCFC;
$color-primary                        : #FFBC00;


$color-error                          : #ff0000;
$color-danger                         : rgb(213, 049, 049); // #d53131: reddish
$color-warn                           : rgb(239, 132, 060); // #ef843c: dusty-orange

// gray
$gray-black                           : rgb(000, 000, 000); // #000000
$gray-darkest                         : rgb(097, 103, 111); // #61676f: slate-grey
$gray-darker                          : rgb(112, 112, 112); // #818891: steel
$gray-dark                            : rgb(174, 174, 174); // #aeaeae: light-grey
$gray                                 : rgb(209, 209, 209); // #D1D1D1:
$gray-light                           : rgb(235, 235, 235); // #EFEFEF: light-gray
$gray-lighter                         : rgb(243, 245, 246); // #f3f5f6: ice-gray
$gray-lightest                        : rgb(241, 243, 249); // #f1f3f9: light-pale-grey
$gray-white                           : rgb(255, 255, 255); // #ffffff

// background
$background-color-primary             :#F8F8F8;


//
// Transition
//

$transition-mode                      : cubic-bezier( .25, .8, .25, 1);


//
// Texting
//

$font-family                          : "Roboto","Lato", "-apple-system", "PingFang-TC", "Segoe UI", "Microsoft JhengHei", "Oxygen", "Fira Sans", "Helvetica Neue", "Helvetica", "Arial", "sans-serif";
$font-logo                            : "Modern No. 20";
$font-size-xl                         : 24px;
$font-size-lg                         : 18px;
$font-size-md                         : 16px;
$font-size-sm                         : 14px;
$font-size-xs                         : 12px;


//
// Usage
//
$mobile-width: 375px;
$table-width: 750px;
$laptop-width: 992px;
$desktop-width: 1200px;

//
// RWD
//

@mixin above-mobile() {
  @media screen and (min-width: $mobile-width) {
    @content;
  }
}

@mixin above-table() {
  @media screen and (min-width: $table-width) {
    @content;
  }
}

@mixin above-laptop() {
  @media screen and (min-width: $laptop-width) {
    @content;
  }
}

@mixin above-desktop() {
  @media screen and (min-width: $desktop-width) {
    @content;
  }
}

@mixin full-fill($position: absolute) {
  position: $position;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

@mixin image-fluid {
  max-width: 100%;
  height: auto;
}

@mixin image-centre($height, $size: "contain", $child: "img") {
  height: $height;
  line-height: $height;
  text-align: center;
  overflow: hidden;

  > #{$child} {
    @include image-centre-child($size);
  }
}

@mixin image-centre-child($size: "contain") {
  margin: -100%;

  @if $size == "contain" {
    max-height: 100%;
    max-width: 100%;
  } @else if $size == "cover-by-width" {
    min-width: 100%;
  } @else if $size == "cover-by-height" {
    min-height: 100%;
  }
}

@mixin position-centre($position: absolute) {
  position: $position;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@mixin position-center-over-top($transform...) {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -100%) #{$transform};
}

@mixin position-center-over-right($transform...) {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(100%, -50%) #{$transform};
}

@mixin position-center-over-bottom($transform...) {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 100%) #{$transform};
}

@mixin position-center-over-left($transform...) {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-100%, -50%) #{$transform};
}

@mixin position-top-over-left($transform...) {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-100%, 0%) #{$transform};
}

@mixin position-top-over-right($transform...) {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(100%, 0%) #{$transform};
}

@mixin position-right-over-top($transform...) {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(0%, -100%) #{$transform};
}

@mixin position-right-over-bottom($transform...) {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translate(0%, 100%) #{$transform};
}

@mixin position-left-over-top($transform...) {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(0%, -100%) #{$transform};
}

@mixin position-left-over-bottom($transform...) {
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translate(0%, 100%) #{$transform};
}

@mixin position-bottom-over-left($transform...) {
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translate(-100%, 0%) #{$transform};
}

@mixin position-bottom-over-right($transform...) {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translate(100%, 0%) #{$transform};
}

@mixin size($width, $height: $width) {
  width: $width;
  height: $height;
}

/**
 * Pure CSS text truncate
 * See [Line Clampin' (Truncating Multiple Line Text) - Weird WebKit Flexbox Way](https://css-tricks.com/line-clampin/)
 */

@mixin text-truncate-singleline($width: 100%) {
  // Requires inline-block or block for proper styling
  // not work at inline-flex and flex

  width: $width;
  white-space: nowrap;
  @include text-truncate-style;
}

@mixin text-truncate-multiline($line-to-show, $line-height: 1.5, $font-size: 1em) {
  display: block; // fallback
  display: -webkit-box;
  line-height: $line-height;
  max-height: $font-size * $line-height * $line-to-show; // fallback
  -webkit-line-clamp: $line-to-show;
  @include text-truncate-style;

  // *** IMPORTANT SEE ***:
  // 1. https://github.com/postcss/autoprefixer/issues/776#issuecomment-392346351
  // 2. https://github.com/NoName4Me/yo-FE/issues/3
  // eslint-disable-next-line
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
}

@mixin text-truncate-style {
  overflow: hidden;
  text-overflow: ellipsis;
}

/**
 * 1. Remove white space between inline-block
 * See [Fighting the Space Between Inline Block Elements](https://css-tricks.com/fighting-the-space-between-inline-block-elements/)
 */

@mixin vertical-align($height: 100%, $font-size: 1rem, $align: middle) {
  height: $height;
  font-size: 0; /* 1 */

  &:before {
    content: "";
    width: 0;
    height: 100%;
    @include vertical-align-style($align);
  }

  > * {
    font-size: $font-size; /* 1 */
    @include vertical-align-style($align);
  }
}

@mixin vertical-align-style($align) {
  display: inline-block;
  vertical-align: $align;
}

.App {
  box-sizing: border-box;
  height: 100vh;
  width: 100%;
  background: $background-color-primary;
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 5%;
  }
  .header {
    display: flex;
    justify-content: center;
    padding: 76px 0 42px 0;
  }
  .content {
    display: flex;
    justify-items: center;
    &--4 {
      position: fixed;
      z-index: 2;
      @include above-laptop {
        z-index: 1;
        position: initial;
        flex: 0 0 calc(100% * 0.3);
        max-width: calc(100% * 0.3);
      }
    }
    &--6 {
      flex: 0 0 calc(100% * 0.66);
      max-width: calc(100% * 0.66);
      margin: 0 auto;
      z-index: 1;
      @include above-laptop {
        flex: 0 0 calc(100% * 0.7);
        max-width: calc(100% * 0.7);
      }
    }
    .product {
      font-size: $font-size-sm;
      position: fixed;
      top: 0;
      left: -530px;
      transition: all 0.5s ease-in-out;
      &--active {
        transition: all 0.5s ease-in-out;
        left: 0;
      }
      @include above-laptop {
        position: relative;
        left: 0%;
        z-index: 1;
      }
      &-zone {
        background-color: $gray-light;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
        border-radius: 11px;
        padding: 15px 45px;
        @include above-laptop {
          margin-top: 60px;
          padding: 45px 65px;
        }
      }
      &-title {
        padding: 10px 0;
      }
      &-content {
        margin-bottom: 20px;
      }
      &-info-button {
        position: absolute;
        background-color: #fff;
        box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.08);
        border-radius: 0 5px 5px 0;
        writing-mode: vertical-lr;
        line-height: 1;
        letter-spacing: 1.5px;
        cursor: pointer;
        right: -40px;
        top: 0;
        padding: 35px 12px;
        @include above-laptop {
          right: initial;
          top: 6.5%;
          left: 0;
        }
      }
    }
  }
}
